import Swiper, { Pagination } from 'swiper';

export default () => {
  let active = document.querySelector('.mainServices__right-button.active');
  if (active != null) {
    active.disabled = true;
  }

  $('.mainServices__right-button').on('click', function () {
    let btns = document.querySelectorAll('.mainServices__right-button');
    console.log('hey');
    btns.forEach((element) => {
      element.disabled = false;
    });

    $('.mainServices__right-button').removeClass('active');
    $('.mainServices__service').removeClass('active');

    let data = this.dataset.service;
    this.disabled = true;
    $(this).addClass('active');
    $('.mainServices__service[data-service=' + data + ']').addClass('active');
  });

  let windowWidth = window.innerWidth;
  let servSwiper;
  let inits = false;

  $(window).on('resize load', function () {
    windowWidth = window.innerWidth;
    if (windowWidth < 768) {
      if (!inits) {
        inits = true;
        Swiper.use([Pagination]);
        servSwiper = new Swiper('#mainServSlider', {
          slidesPerView: 1,
          speed: 400,
          spaceBetween: 30,
          autoHeight: true,
          pagination: {
            el: '#mainServPage',
            type: 'bullets',
            clickable: true,
          },
        });
      }
    } else {
      if (inits) {
        inits = false;
        servSwiper.destroy();
      }
    }
  });
};
