import Swiper, { Navigation } from 'swiper';
export default () => {
  Swiper.use([Navigation]);
  let swiper = new Swiper('#trustSlider', {
    slidesPerView: 1,
    speed: 400,
    // centeredSlides: true,
    spaceBetween: 15,
    navigation: {
      prevEl: '#trustSliderPrev',
      nextEl: '#trustSliderNext',
    },
    breakpoints: {
      768: {
        slidesPerView: 2,
        // spaceBetween: 30
      },
      1024: {
        slidesPerView: 3,
        // spaceBetween: 30
      },
    },
  });
};
